.skillsBlock {
    min-height: 80vh;
    width: 100%;
    background-color: aquamarine;
    display: flex;
    align-items: center;
}

.skillsContainer {
    justify-content: center;
    align-items: center;
}

.skills {
    border: 1px solid;
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
}